@import '_tokens.scss';
@import '_functions.scss';

.t-buttons-container {
    display: inline-block;
    width: 100%;

    @include device(tablet) {
        padding: $spaceXXXSmall 0 $spaceXXXSmall 0;
        font-size: $FontSizeTextNormal;
        display: flex;
    }
}

.t-button {
    display: flex;
    flex: 1;
    padding: $spaceNormal;
    margin: $spaceXSmall 0;
    font-size: $FontSizeTextSmall;
    border-radius: $borderRadiusLarge;
    text-decoration: none;
    align-self: center;
    justify-content: center;
    margin-right: $spaceMedium;
    cursor: pointer;

    &:hover,
    &--active {
        text-decoration: none;
    }

    @include device(tablet) {
        flex: 0 auto;
        padding: $spaceXXXSmall $spaceMedium $spaceXXXSmall $spaceMedium;
        font-size: $FontSizeTextNormal;
    }

    @include device(desktop) {
        padding: $spaceXXSmall $spaceMedium $spaceXXSmall $spaceMedium;
    }

    &:link,
    &:focus,
    &:hover,
    &--active,
    &:active {
        text-decoration: none !important;
    }

    img {
        margin-right: $SizeXXSmall;
    }

    i {
        align-self: center;
        margin-right: $SizeXXSmall;
        opacity: 0.5;
    }
}

.t-button-primary {
    @extend .t-button;
    background-color: $colorsBaseProductNormal;
    color: $colorsBaseWhiteNormal;
    cursor: pointer;
    align-self: center;

    &:hover,
    &--active {
        color: $colorsBaseWhiteNormal;
        background-color: $colorsBaseProductNormalActive;
    }

    &:link,
    &:focus,
    &:hover,
    &--active,
    &:active {
        color: $colorsBaseWhiteNormal;
    }
}

.t-button-secondary {
    @extend .t-button;
    background-color: $colorsBaseCloudNormal;
    color: $colorsBaseInkNormal;

    &:hover,
    &--active, &:focus {
        color: $colorsBaseInkNormal;
        background-color: $colorsBaseCloudLightHover;
    }
}

.t-button-tertiary {
    @extend .t-button;
    background-color: $colorsBaseInkLighterActive;
    color: $colorsBaseWhiteNormal;

    &:hover,
    &--active {
        color: $colorsBaseWhiteNormal;
        background-color: $colorsBaseInkLight;
    }
}

.t-button-submit {
    @extend .t-button-primary;
    margin: $SizeNormal 0;
}

.t-button-danger {
    @extend .t-button;
    color: $colorsBaseWhiteNormal;
    background-color: $colorsTextError;
    &--disabled {
        background-color: $colorsBaseCloudNormal;
        color: $colorsBaseInkLighterActive;
        cursor: not-allowed !important;
    }
 
    &:hover {
        color: $colorsBaseWhiteNormal;
    }
       
    &--disabled:hover {
        color: $colorsBaseInkLighterActive;
    }
}